import HandleRawData from './handleRawData';
import DataObject from './dataObject';

class FinalData {
    processData(conf, prevBase, currBase, prevDrivers, currDrivers, prevPercents, currPercents) {
        let handleRawData = new HandleRawData();
        let fineConf = handleRawData.handleConf(conf);
        let finePrevBase = handleRawData.handleBase(prevBase.prevBases);
        let fineCurrBase = handleRawData.handleBase(currBase.currBases);
        let finePrevDrivers = handleRawData.handleDrivers(prevDrivers.prevDrivers);
        let fineCurrDrivers = handleRawData.handleDrivers(currDrivers.currDrivers);
        let finePrevPercents = handleRawData.handlePercents(prevPercents.prevPercents);
        let fineCurrPercents = handleRawData.handlePercents(currPercents.currPercents);

        let dataObject = new DataObject();
        let dataReady = dataObject.generateData(fineConf, finePrevBase, fineCurrBase, finePrevDrivers, fineCurrDrivers, finePrevPercents, fineCurrPercents);
        
        return {
            data: dataReady
        }
    }
}

export default FinalData;
