import React from 'react';
import './SingleTable.css'

import TableRow from './TableRow/TableRow';

const SingleTable = (props) => {
    return (
        <table className="SingleTable">
            <thead>
                <tr>
                    <th>Drivers</th>
                    <th className={props.data.data.prevBase < 50 ? 'lowbase' : null}>{props.data.data.prevBase < 50 ? props.data.data.prevBase + ' *' : props.data.data.prevBase}</th>
                    <th className={props.data.data.currBase < 50 ? 'lowbase' : null}>{props.data.data.currBase < 50 ? props.data.data.currBase + ' *' : props.data.data.currBase}</th>
                </tr>
            </thead>

            <tbody>
                { props.data.data.rows.map((r, i) => {
                    return (
                        <TableRow
                            key={i}
                            driverValue={props.data.data.rows[i].driver}
                            prevValue={props.data.data.rows[i].prevValue}
                            currValue={props.data.data.rows[i].currValue}
                            statColor={props.data.data.rows[i].statColor}
                            idx={i}
                        />
                    )
                }) }
            </tbody>
        </table>
    )
}

export default SingleTable;
