import React from 'react';
import './TableRow.css';

const TableRow = (props) => {
    return (
        <tr>
            <td key={props.idx} >{props.driverValue}</td>  
            <td >{props.prevValue}</td>
            <td className={props.statColor} >{props.currValue}</td>
        </tr>
    )
}

export default TableRow;
