import React from 'react';
import './SingleForm.css';

const SingleForm = (props) => {
    return (
        <form onSubmit={props.formSubmited} className="SingleForm">
            <div className="form-field">
                <label htmlFor="confidence">Confidence Level</label>
                <select id="confidence" name="conf" onChange={props.formChanges} defaultValue="95%">
                    <option value="60%">60%</option>
                    <option value="70%">70%</option>
                    <option value="80%">80%</option>
                    <option value="85%">85%</option>
                    <option value="90%">90%</option>
                    <option value="95%">95%</option>
                    <option value="98%">98%</option>
                    <option value="99%">99%</option>
                    <option value="99.8%">99.8%</option>
                    <option value="99.9%">99.9%</option>
                </select>
            </div>

            <div className="form-group">
                <div className="form-field">
                    <input 
                        type="text" 
                        name="prevBases" 
                        onChange={props.formChanges}
                        required 
                        placeholder="Base sizes previous wave"></input>
                </div>
                <div className="form-field">
                    <input 
                        type="text" 
                        name="currBases" 
                        onChange={props.formChanges}
                        required 
                        placeholder="Base sizes current wave"></input>
                </div>
            </div>
            
            <div className="form-group">
                <div className="form-field">
                    <textarea 
                        rows="2" 
                        name="prevDrivers" 
                        onChange={props.formChanges}
                        placeholder="Drivers previous wave"></textarea>
                </div>
                <div className="form-field">
                    <textarea 
                        rows="2" 
                        name="currDrivers" 
                        onChange={props.formChanges}
                        placeholder="Drivers current wave"></textarea>
                </div>
            </div>

            <div className="form-group">
                <div className="form-field">
                    <textarea rows="5" name="prevPercents" onChange={props.formChanges} required placeholder="Percents previous wave"></textarea>
                </div>
                <div className="form-field">
                    <textarea rows="5" name="currPercents" onChange={props.formChanges} required placeholder="Percents current wave"></textarea>
                </div>
            </div>
            
            <div className="form-field">
                <button>Calculate</button>
            </div>
        </form>
    )
}

export default SingleForm;
