import React, { useState } from 'react';
import './SingleTest.css';

import SingleForm from '../../components/OneVSOne/SingleForm/SingleForm';
import SingleTable from '../../components/OneVSOne/SingleTable/SingleTable';
import DataService from '../../services/single-data/finalData';

const SingleTest = () => {
    const [conf, setConf] = useState({
        conf: "95%"
    });
    const [prevBases, setPrevBases] = useState({
        prevBases: ""
    });
    const [currBases, setCurrBases] = useState({
        currBases: ""
    });
    const [prevDrivers, setPrevDrivers] = useState({
        prevDrivers: ""
    });
    const [currDrivers, setCurrDrivers] = useState({
        currDrivers: ""
    });
    const [prevPercents, setPrevPercents] = useState({
        prevPercents: ""
    });
    const [currPercents, setCurrPercents] = useState({
        currPercents: ""
    });
    const [isValid, setisValid] = useState({
        valid: false
    });
    const [data, setData] = useState({
        data: {}
    });

    const handleChange = (event) => {
        if (event.target.name === "conf") {
            setConf({
                conf: event.target.value
            });
        } else if (event.target.name === "prevBases") {
            setPrevBases({
                prevBases: event.target.value
            });
        } else if (event.target.name === "currBases") {
            setCurrBases({
                currBases: event.target.value
            });
        } else if (event.target.name === "prevDrivers") {
            setPrevDrivers({
                prevDrivers: event.target.value
            });
        } else if (event.target.name === "currDrivers") {
            setCurrDrivers({
                currDrivers: event.target.value
            });
        } else if (event.target.name === "prevPercents") {
            setPrevPercents({
                prevPercents: event.target.value
            });
        } else if (event.target.name === "currPercents") {
            setCurrPercents({
                currPercents: event.target.value
            });
        }
    }

    const handleSubmit = (event) => {
        event.preventDefault();
        let dataService = new DataService();
        let wholeData = dataService.processData(conf, prevBases, currBases, prevDrivers, currDrivers, prevPercents, currPercents);
        setisValid({ valid: true });
        setData({data: wholeData})
    }

    return (
        <section className="SingleTesting">
            <SingleForm formSubmited={handleSubmit} formChanges={handleChange} />
            {
                isValid.valid ?
                    <SingleTable data={data.data} /> :
                    null
            }
        </section> 
    )
}

export default SingleTest;
