import React from 'react';
import './App.css';
import { Route, Switch, Redirect } from 'react-router-dom';

import Navigation from './components/Navigation/Navigation';
import MultiTest from './containers/multi-test/MultiTest';
import SingleTest from './containers/single-test/SingleTest';

function App() {
  return (
    <div className="App">
      <Navigation />
      <Switch>
        <Route path="/" exact render={() => <Redirect to="/one-vs-one" />} />
        <Route path="/one-vs-one" component={SingleTest} />
        <Route path="/many-vs-many" component={MultiTest} /> 
      </Switch>
    </div>
  );
}

export default App;
