import React from 'react';
import { NavLink } from 'react-router-dom';

import './Navigation.css';

const Navigation = () => {

    return (
        <header>
            <nav>
                <ul>
                    <li>
                        <NavLink to="/one-vs-one">One vs One</NavLink>
                    </li>
                    <li>
                        <NavLink to="/many-vs-many">Many vs Many</NavLink>
                    </li>
                </ul>
            </nav>
        </header>
    )
};

export default Navigation;
