import StatTesting from './statTesting';

class DataObject {
    generateData(conf, prevBase, currBase, prevDrivers, currDrivers, prevPercents, currPercents) {
        let finalDataObject = {};
        finalDataObject['prevBase'] = prevBase;
        finalDataObject['currBase'] = currBase;
        finalDataObject['rows'] = [];

        if (prevDrivers && currDrivers) {
            for (let r = 0; r < currDrivers.length; r++) {
                let targetIndex = prevDrivers.indexOf(currDrivers[r]);
                let tempObj = {driver: currDrivers[r], currValue: currPercents[r]};
                if (targetIndex < 0) {
                    tempObj['prevValue'] = '-';
                } else {
                    tempObj['prevValue'] = prevPercents[targetIndex];
                }

                finalDataObject.rows.push(tempObj);
            }
        } else {
            // here you need to manually assure count of values in both columns should match
            for (let r = 0; r < currPercents.length; r++) {
                let tempObj = {driver: currDrivers[r] || "<->", currValue: currPercents[r], prevValue: prevPercents[r]};
                finalDataObject.rows.push(tempObj);
            }
        }

        // SET STAT TESTS
        let statTesting = new StatTesting();
        for (let i = 0; i < finalDataObject.rows.length; i++) {
            finalDataObject.rows[i]['statColor'] = statTesting.calcStatTest(prevBase, currBase, finalDataObject.rows[i].prevValue, finalDataObject.rows[i].currValue, conf)
        };


        // SET DATA FOR RENDERING
        for (let r = 0; r < finalDataObject.rows.length; r++) {
            if (finalDataObject.rows[r].prevValue === -100) {
                finalDataObject.rows[r].prevValue = '-'; 
            } else {
                finalDataObject.rows[r].prevValue = parseFloat((finalDataObject.rows[r].prevValue * 100).toFixed(7)) + ' %';
            }
            if (finalDataObject.rows[r].currValue === -100) {
                finalDataObject.rows[r].currValue = '-'; 
            } else {
                finalDataObject.rows[r].currValue = parseFloat((finalDataObject.rows[r].currValue * 100).toFixed(7)) + ' %';
            }
        }

        return finalDataObject;
    }
}

export default DataObject;
